.home-container {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: #0a1a4a;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.header {
  background-color: #e6e6e6; /* corrected header background color */
  display: flex;
  flex-direction: column; /* changed from row to column to stack logo and nav */
  align-items: center; /* center items horizontally */
  justify-content: center;
  padding: 15px 20px;
  border-bottom: 1px solid #ccc;
  width: 100%; /* changed from 100vw to 100% to avoid horizontal scroll */
  box-sizing: border-box;
}

.logo {
  width: 130px;
  margin: 0 0 10px 0; /* margin bottom for spacing below logo */
  display: block;
}

.nav-menu {
  display: flex;
  gap: 30px;
  font-weight: 600;
  font-size: 18px;
  justify-content: center; /* center nav links horizontally */
  flex: none;
  margin-top: 0; /* removed margin-top to align vertically with logo */
}

.nav-menu a {
  color: #0a1a4a;
  text-decoration: none;
  transition: color 0.3s ease;
}

.nav-menu a:hover,
.nav-menu a:focus {
  color: #0077cc;
  outline: none;
}

.carousel {
  position: relative;
  margin-top: 25px;
  overflow: hidden;
  max-height: 340px;
  border-radius: 10px;
  box-shadow: 0 5px 10px rgba(10, 26, 74, 0.15);
}

.slide-image {
  width: 100%;
  height: 340px;
  object-fit: cover;
  display: block;
  border-radius: 10px;
}

.arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background-color: rgba(10, 26, 74, 0.7);
  border: none;
  color: white;
  font-size: 34px;
  padding: 7px 16px;
  cursor: pointer;
  user-select: none;
  border-radius: 5px;
  z-index: 10;
  transition: background-color 0.3s ease;
}

.arrow:hover,
.arrow:focus {
  background-color: rgba(10, 26, 74, 1);
  outline: none;
}

.left-arrow {
  left: 14px;
}

.right-arrow {
  right: 14px;
}

.dots {
  text-align: center;
  margin-top: 14px;
}

.dot {
  height: 16px;
  width: 16px;
  margin: 0 7px;
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.dot.active {
  background-color: #0a1a4a;
}

.features {
  display: flex;
  justify-content: space-around;
  margin: 55px 0;
  text-align: center;
  gap: 45px;
}

.feature-item {
  flex: 1;
  max-width: 190px;
}

.feature-item img {
  width: 75px;
  margin-bottom: 14px;
}

.feature-item p {
  font-weight: 700;
  font-size: 20px;
  margin: 0;
}

.contact {
  display: flex;
  justify-content: space-between;
  background-color: #e6e6e6;
  padding: 30px 45px;
  border-top: 1px solid #ccc;
  border-radius: 0 0 10px 10px;
  box-sizing: border-box;
  gap: 25px;
}

.contact-info {
  font-size: 16px;
  font-weight: 700;
  color: #333;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 8px;
}

.map img {
  width: 420px;
  height: auto;
  border-radius: 10px;
  box-shadow: 0 0 15px rgba(0,0,0,0.15);
}

@media (max-width: 768px) {
  .features {
    flex-direction: column;
    gap: 35px;
  }
  .contact {
    flex-direction: column;
    gap: 25px;
  }
  .map img {
    width: 100%;
  }
}
