.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Cabeçalho fixo no topo (logo + menu) */
.header {
  background-color: #e8e8e8;
  padding: 20px 40px;
  display: flex;
  flex-direction: column; /* Empilha a logo e o menu verticalmente */
  align-items: center; /* Centraliza tudo horizontalmente */
}

/* Logo da empresa */
.logo {
  height: 60px;
  margin: 0 auto; /* Remove margem lateral fixa */
}

/* Lista de navegação (ul) com os itens de menu */
.nav-list {
  list-style: none; /* Remove bolinhas */
  display: flex;
  gap: 30px; /* Espaçamento entre os itens */
  margin-top: 20px; /* Espaço entre logo e menu */
  padding: 0;
}

/* Cada item do menu (li) */
.nav-list li {
  font-weight: bold;
  font-size: 16px;
  color: #0c1d5e;
  cursor: pointer;
  transition: color 0.2s;
}

/* Efeito de hover no menu */
.nav-list li:hover {
  color: #2f3757;
}
